.settings_container {
    position: absolute;
	display: flex;
	flex-direction: column;
    justify-content: flex-start;
	overflow: auto;
    width: 100%;
	height: 100%;
    z-index: 150;
}

.settings_toggle {
    position: absolute;
    vertical-align: top;
    right: 8px;
    padding: 0.5rem;
	font-size: 1.5rem;
    cursor: pointer;
    border: none;
    background: none;
    color: var(--font-color);
    z-index: 150;
}
